import linguas from '@/data/linguagem.json'
import axios from '@/libs/axios'

export default {
    namespaced: true,
    state:{
        calendar:new Date(),
        dica:'Não leve a vida tão a sério, afinal ninguém vai sair vivo dela'

     },
    getters:{
        dicaDoDia:({ dica }) => {

            return dica
        },
        calendarioEmPortugues:({ calendar }) => {

            return new Date(calendar).toLocaleDateString('pt-br',{dateStyle:'full'})
        }
    },
    mutations: {
        COMMIT_CALENDAR({calendar}, payload){
            calendar = payload
        }

     },
    actions:{

        async getDateFromServer({ commit }){
            new Promise(async (resolve,reject) => {
                try {
                    
                    const query = (await axios.get('/calendar')).data
                
                    commit('COMMIT_CALENDAR', query)

                    resolve(query)
                
                
                } catch (error) {
                    
                reject(error)                    
                    
                }

            })
        }
        
     }
}