<template>
  
        
    <v-form ref="form"  v-model="valid" lazy-validation @submit.prevent="procedimento">

            <v-card   outlined>
                
                <v-card-text>
                    
                    
                    <v-row g-5>
                        <v-col cols="12" sm="6">
                            <v-select 
                            outlined 
                            required
                            :disabled="loading"
                            :loading="loading"
                            menu-props="auto"
                            single-line
                            :rules="[v => !!v || 'informe o idioma']"
                            :items="linguas" v-model="de.lingua" item-text="nome" item-value="lingua"
                                label="Traduzir de">
                            </v-select>

                            <v-textarea 
                            :disabled="loading"
                            :loading="loading"
                            @input="para.palavra = 'escrevendo...'"
                            @focus="para.palavra = '...'"
                            @blur="para.palavra = ''"
                            :rules="[v => !!v || 'informe a palavra']"
                           
                            required outlined @change=" v => de.palavra = v" placeholder="Digite a palavra" counter
                                :autoGrow="true" clearable></v-textarea>
                        </v-col>
                        <v-col cols="12" sm="6">
                            <v-select 
                            outlined
                            required
                            :disabled="loading"
                            :loading="loading"
                            menu-props="auto"
                            single-line
                         
                            :rules="[v => !!v || 'informe a idioma']"
                            :items="linguas" v-model="para.lingua" item-text="nome" item-value="lingua"
                            label="Traduzir para"> 
                        </v-select>
                            <v-textarea 
                            :disabled="loading"
                            :loading="loading"
                         
                            outlined placeholder="tradução" 
                            v-model="para.palavra" 
                            readonly :autoGrow="true">
                            </v-textarea>
                        </v-col>
                    </v-row>
    
                </v-card-text>
                <v-card-actions>
                    <v-btn 
                    :disabled="loading"
                    :loading="loading"
                    type="submit" color="success">Traduzir</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn 
                    :disabled="loading"
                    :loading="loading"
                    @click="$refs.form.reset()" class="white--text" color="red"> {{'Limpar'}} </v-btn>
                </v-card-actions>
            </v-card>
        </v-form>
    
  
</template>
<script>
    import { mapState,mapGetters,mapActions } from 'vuex'
    export default {
        data() {
            return {
                valid: true,
                
                
            }
        },
        watch:{
            'para.lingua':{
                handler(newValue,prevValue){

                  //  this.procedimento()

                    if(!!this.para.palavra) this.procedimento()

                   // this.translate()


                    // alert(newValue)
                },
                immediate:false
            },
            'de.palavra':{
                handler(newValue,prevValue){



                    // alert(newValue)
                },
                immediate:false
            }

        },
        computed:{

            de:{
                get: function () {
                return this.$store.state.dicionario.primario
                },
                set:function(value){
                    this.$store.state.dicionario.primario = value
                }
            },
            para:{
                get:function(){
                    return this.$store.state.dicionario.secondario
                },
                set:function(value){
                    this.$store.state.dicionario.secondario = value
                }
            },
            ...mapGetters({
                disableLang:'dicionario/disabilitarLinguaSelec',
                linguas:'dicionario/linguas',
                loading:'dicionario/l',

             
            }),
        },
        methods: {
            async procedimento(){
                try {
                    
                    const decision = await this.$refs.form.validate()
                    
                    if(!decision) throw 'preencha campo em falta'

                    await this.translate()

                } catch (error) {

                    this.para.palavra = error
                
                  
                }



                
            },
            ...mapActions({
                translate:'dicionario/traduzir'
            }),
        },
        mounted(){


        },
        beforeDestroy() {
            
            this.$refs.form.reset()
        },


    }

</script>