import linguas from '@/data/linguagem.json'
import axios from '@/libs/axios'

export default {
    namespaced: true,
    state:{
        linguas,
        loading:false,
        primario:{
            lingua:'',
            palavra:'',
        },
        secondario:{
            lingua:'',
            palavra:'',
        }

    },
    getters:{
        disabilitarLinguaSelec:({ linguas,primario }) => {
            
            return linguas.indexOf((linguas.filter( v => primario.lingua === v.lingua ))[0])
        },
        l:({ loading }) => {

            return loading
        },
        linguas:({ linguas }) => {

            return linguas
        }

    },
    mutations: {
        COMMIT_LOADING({ loading }, payload){
            loading = payload
        },
        COMMIT_TRADUCAO({ secondario }, payload){
            secondario.palavra = payload.significado
        }
    },
    actions:{
        async traduzir({ getters, state,commit }){
            return new Promise(async (resolve,reject) => {
                try {
                    commit('COMMIT_LOADING', true)
                    
                    /* "lingua":"portugues"
                "lingua":"chokwe"
            "lingua":"umbundu" */

    
            
                const result = await axios.get('/translate/universal',{params: {
                    key: state.primario.lingua,
                    palavra: state.primario.palavra,
                    para: state.secondario.lingua
                }})

                if(!result.data.response) throw result.data.msg

                commit('COMMIT_TRADUCAO', result.data.query)
              //  commit('COMMIT_LOADING',false)
                
        
            resolve({response:result.data.response, msg:'ok' })
        
        } catch (error) {
            
         //   commit('COMMIT_LOADING',false)
    
            reject(error)
            
        }
            
            
        })
        }
        
    }
}